import { Movie } from "./Movie";
import  NotFound  from "./NotFound.png"


function Movies(props) {
    const { movies = [] } = props;

    return (
        <div className="movies">
            { movies.length ? (
                movies.map((movie) => <Movie key={movie.imdbID} {...movie} /> )
            ) : (
                <div className="notf">
                    <h4>Not Found</h4>
                    <img src={NotFound} alt="Котик" />
                </div>
                
            ) }
        </div>
     );
} 

export { Movies };